<template>
  <div class="login">
    <mHeader head-title="会员登录"></mHeader>
    <div class="loginContent">
      <div class="loginRegister">
        <van-tabs :active="active">
          <van-tab>
            <div slot="title">登录</div>
            <div class="loginDetail">
              <div class="contact">
                <ul>
                  <li>
                    <i>+86</i>
                    <input class="inputTel" type="tel" placeholder="输入手机号码">
                  </li>
                  <li>
                    <div>
                      <input type="text" placeholder="输入验证码">
                    </div>
                    <div>
                      <input class="countdown" type="text" placeholder="获取验证码" disabled>
                    </div>
                  </li>
                </ul>
              </div>
              <div class="commit" @click="loginCommit">
                <span>提交</span>
              </div>
            </div>
          </van-tab>
          <van-tab>
            <div slot="title">注册</div>
            <div class="loginDetail">
              <div class="contact">
                <ul>
                  <li>
                    <i>+86</i>
                    <input class="inputTel" type="tel" placeholder="输入手机号码">
                  </li>
                  <li>
                    <div>
                      <input type="text" placeholder="输入验证码">
                    </div>
                    <div>
                      <input class="countdown" type="text" placeholder="获取验证码" disabled>
                    </div>
                  </li>
                </ul>
              </div>
              <div class="commit" @click="register">
                <span>注册</span>
              </div>
            </div>
          </van-tab>
        </van-tabs>

      </div>
    </div>
    <!--<div class="otherLogin">

    </div>-->
    <!--<otherLogin/>-->
  </div>
</template>

<script>
  import mHeader from '../../components/header/headerTab.vue'
  export default {
    data(){
      return{
        active:0,
      }
    },
    methods:{
      loginCommit(){
        alert('登录')
      },
      register(){
        alert('注册')
      }
    },
    components:{
      mHeader,
//      otherLogin
    }
  }

</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .login{
    background: red;
    .loginContent{
      margin-top: .88rem;
      .loginDetail{
        background: #13120e;
        padding-top: 1.5rem;
        padding-left: .66rem;
        padding-right: .66rem;
      }
      .contact{
        ul{
          padding: .16rem 0;
          li{
            position: relative;
            margin: .3rem 0;
            height: .8rem;
            input{
              position: absolute;
              background: rgba(49,49,49,0.8);
              border-radius: .1rem;
              width: 100%;
              height: .8rem;
              @include sc(.26rem, #707070);
              text-indent: .2rem;
            }
            .inputTel{
              padding-left: .8rem;
            }
            img{
              position: absolute;
              top: .32rem;
              right: .32rem;
              @include wh(.2rem, .16rem);
            }
            i{
              @include sc(.26rem, #fff);
              @include ct;
              position: absolute;
              padding-left: .1rem;
              border-right: solid 3px #fff;
              padding-right: .2rem;
            }
          }
          li:nth-child(2){
            @include fjb;
            div{
              width: 2.96rem;
              input{
                width: 2.9rem;
              }
            }
            .countdown{
              background: #b7b7b6;
              text-align: center;
              @include sc(.3rem, red)
            }
          }
        }
      }
      .commit{
        @include fjc;
        @include wh(100%, .88rem);
        border: solid 3px #d4be8c;
        border-radius: .4rem;
        margin-top: .9rem;
        span{
          @include sc(.32rem, #d4be8c);
        }
      }
    }
    otherLogin{
      position: fixed;
      bottom: 0;
      left: 0;
      right:0;
    }
  }

</style>



